<template>
  <div>
    <div class="param-info" v-if="Object.keys(paramInfo).length !== 0">
    <p>参数信息</p>
      <table
      :key="index"
      class="info-size"
      v-for="(table, index) in paramInfo.rule.tables"
    >
      <tr :key="indey" v-for="(tr, indey) in table">
        <td :key="indez" v-for="(td, indez) in tr">{{ td }}</td>
      </tr>
    </table>

    <table class="info-param">
      <tr v-for="(info, index) in paramInfo.info.set">
        <td class="info-param-key">{{ info.key }}</td>
        <td class="param-value">{{ info.value }}</td>
      </tr>
    </table>

    <div class="info-img" v-if="paramInfo.info.image">
      <img :src="paramInfo.info.image[0]" alt="" />
    </div>
  </div>
  </div>
</template>

<script>
export default {
  name: 'detailParamInfo',

  data () {
    return {
    }
  },

  methods: {},
  props:{
    paramInfo:{
      type: Object,
      default(){
        return {}
      }
    }
  }
}
</script>

<style scoped>
  .param-info {
  padding: 20px 15px;
  font-size: 14px;
  border-bottom: 5px solid #f2f5f8;
}
.param-info p {
  font-size: 15px;
  margin-bottom: 10px;
}
.param-info table {
  width: 100%;
  border-collapse: collapse;
}
.param-info table tr {
  height: 42px;
}
.param-info table tr td {
  border-bottom: 1px solid rgba(100, 100, 100, 0.1);
}
.info-param-key {
  /*当value的数据量比较大的时候, 会挤到key,所以给一个固定的宽度*/
  width: 95px;
}
.info-param {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}
.param-value {
  color: #eb4868;
}
.info-img img {
  width: 100%;
}
</style>
